@import url(https://fonts.googleapis.com/css2?family=Barlow:wght@600&display=swap);
body {
  font-family: 'Barlow', sans-serif;
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

#root {
  --window-height: calc(100vh - 2rem);
}

code {
  white-space: pre;
}
.MonitorPanel{height:var(--window-height);display:grid;grid-template-rows:1fr;margin:1rem;position:relative}.MonitorPanel button{position:absolute;left:0.5rem;top:0.5rem}.status.free{background:#93E0FF}.status.busy{background:#C80037}.status.offline{background:#ccc}.Monitor{display:grid;grid-template-columns:1fr 1fr 1fr;grid-template-rows:2fr 1fr;grid-gap:1rem;gap:1rem}.Monitor:not(.busy) .status{grid-area:1 / 1 / span 2 / span 3}.Monitor.busy .status{grid-area:1 / 1 / span 1 / span 3}.Monitor h1{position:absolute;margin:.25rem;font-size:1rem;color:#fff;right:0.5rem;top:0.5rem}svg{width:100%;height:100%}.Picker{position:absolute;width:100%;height:100%;background:#fff}

